.App {
  /* text-align: center; */
  background-color: rgb(240, 241, 210);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App{
  border: 2px solid black;
  border-radius: 10px;
  box-shadow: 5px 5px;
  width: 95vw;
  height: 90vh;
  align-self: auto;
  margin-left: auto;
  justify-self: auto;
  margin-right: auto;
  margin-top: 5vh;
  margin-bottom: auto;
}