
/* HEADER */

.header-container{
    /* border: 2px solid black; */
    text-align: center;
    border-bottom: 2px solid black;
    margin: 1rem 1rem 1rem 1rem ;
    /* border-radius: 10px; */
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    padding: 0px 20px 2px 20px;
    box-sizing: border-box;
    height: 12vh;
}

.header-container ul {
    display: flex;
    flex-flow: row;
    list-style: none;
}

.header-container ul li {
    margin: 0.5rem 1rem 1rem 1rem;
    font-size: xx-large;
    padding: 1rem;
    
}
.header-container ul li:hover {
    /* border-radius: 5px;
    box-sizing: border-box;
    height: 4rem;
    width: 15rem;
    padding: 0px 0px 0px 0px; */
    transform: scale(1.1);
   cursor: default;
    border-radius: 5px;
    background-color: rgb(67, 67, 67);
    color: rgb(240, 241, 210);
    
}

.home-name h1 {
    /* border : 1px solid black; */

    border-radius: 5px;
    box-sizing: border-box;
    padding: 5px 0px 0px 0px;
    width: 20rem;
    height: 4rem;
    background-color: rgb(67, 67, 67);
    color: rgb(240, 241, 210);
}

.home-name h1:hover {
   transform: scale(1.02);
   cursor: default;
}

/* About */


.about-container{
    display: flex;
    flex-flow: row ;
    box-sizing: border-box;
}

.about-details{
    border-right:20px dotted rgb(240, 241, 210);
    box-sizing: border-box;
    align-self: auto;
    border-radius: 5px;
    border-top-left-radius: 100px;
    border-bottom-left-radius:100px ;
    border-bottom-right-radius: 50px;
    border-top-right-radius: 50px;

    margin: auto auto;
    background-color: rgb(67, 67, 67);
    width:66%;
    height: 66vh;
    display: flex;
    flex-flow: row;
}
