.about-details p{
   font-size: 5ch ;
   color: white;
   font-family: Georgia, 'Times New Roman', Times, serif;
   padding: 0px 0px 0px 0px;
   
}
.about-details p span{
    border-bottom: 3px solid rgb(240, 241, 210) ;
    font-size: 2.5ch ;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
 }

 .about-text{
    border-right: 10px dotted rgb(240, 241, 210);
    box-sizing: border-box;
    /* margin-left:-20rem ; */
    display: flex;
    flex-flow: column;
    justify-content: left;
    padding-left: 5rem;
    width: 70%;
    height: 100%;
}
.about-graphics{
    /* border: 2px solid #000000; */
    box-sizing: border-box;
    align-self: auto;
    margin: auto auto;
    display: flex;
    flex-flow: wrap;
    width:30%;
    height: 66vh;
}

.about-barcode{
    
    /* border: 2px solid black; */

    content: "";
    /* width: 30%;
    height: 100%; */
    border: 5px double rgb(240, 241, 210);
    border-radius: 20px;
    width: 15vw;
    height: 40vh;
    margin:5rem 1rem 1rem 2rem;
}

.barcode{
    font-family: 'Libre Barcode 128', cursive;
    font-size:15ch;
   
   
}


