/* Footer */
.footer-container{
    text-align: center;
    height: 10vh;
}



.skill-tab{
    height: 3rem;
    width: max-content;
    text-align: justify;
    /* border: 1px solid black; */
    box-sizing: border-box;
    margin: 1rem 1rem;
    border-radius: 0.3rem;
    font-size: x-large;
    padding: 0.5rem 0.5rem 0rem 0.5rem;
    font-weight: bolder;
    color: rgb(67, 67, 67);
    color: wheat;
    justify-content: center;
    align-items: center;
}

.skills-container{
    display: flex;
    flex-wrap: wrap;
}